<template>
  <v-card
    height="100%"
  >
    <div
      id="mafs-institutos"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Institutos y Unidades de Gestión.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('mafs-institutos')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los cargos maf.</v-card-subtitle>

      <v-card-text>
        <v-responsive>
          <GChart
            :data="chartData"
            :options="chartOptions"
            type="PieChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Instituto
                </th>
                <th class="text-left">
                  Cargos
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in institutosData"
                :key="index"
              >
                <td>{{ parseInstitutos(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['mafs', 'mobileSize']),
    chartData () {
      return [
        ['Instituto', 'Cargos'],
        ['ICI', this.institutosData.ICI],
        ['IDH', this.institutosData.IDH],
        ['IDEI', this.institutosData.IDEI],
        ['ICO', this.institutosData.ICO],
        ['Secretaría de cultura y medios', this.institutosData.CULT]
      ]
    },
    chartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 40,
          top: 40,
          bottom: 40
        },
        colors: this.colors,
        backgroundColor: { fill: 'transparent' },
        height: 350,
        bar: { groupWidth: '70%' },
        pieHole: '0.2',
        sliceVisibilityThreshold: 0.0001,
        legend: {
          position: 'right',
          alignment: 'center',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    institutosData () {
      return this.mafs.reduce((acc, cur) => {
        return cur.unidad_academica === 'ICI'
          ? { ...acc, ICI: acc.ICI + 1 }
          : cur.unidad_academica === 'IDH'
            ? { ...acc, IDH: acc.IDH + 1 }
            : cur.unidad_academica === 'IDEI'
              ? { ...acc, IDEI: acc.IDEI + 1 }
              : cur.unidad_academica === 'ICO'
                ? { ...acc, ICO: acc.ICO + 1 }
                : cur.unidad_academica === 'CULT'
                  ? { ...acc, CULT: acc.CULT + 1 }
                  : acc
      },
      {
        ICI: 0,
        IDH: 0,
        IDEI: 0,
        ICO: 0,
        CULT: 0
      }
      )
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },
    parseInstitutos (v) {
      return v === 'ICI'
        ? 'ICI'
        : v === 'IDH'
          ? 'IDH'
          : v === 'IDEI'
            ? 'IDEI'
            : v === 'ICO'
              ? 'ICO'
              : v === 'CULT'
                ? 'Secretaría de cultura y medios'
                : 'No declara'
    }
  }
}
</script>
